import axios from "axios";

export default {
  props: ["photoData"],

  methods: {
    prev() {
      this.$emit("prev");
    },

    next() {
      this.$emit("next");
    },

    closed() {
      this.$emit("closed");
    },
    download(img) {
      axios({
        method: "get",
        url: img.url,
        responseType: "blob",
      }).then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", img.name || "image");
        document.body.appendChild(link);
        link.click();
      });
    },
  },

  watch: {
    photoData: function () {
      if (this.photoData?.media.url != null) {
        this.$bvModal.show("modal-center");
      }
    },
  },
  computed: {
    getPhotoUrl() {
      return this.photoData?.media.url;
    },
    getPhotoDescription() {
      return this.photoData?.description || "";
    },
    getPhotoDate() {
      const d = new Date(this.photoData?.created_at);
      const yyyy = d.getFullYear();
      let mm = d.getMonth() + 1; // Months start at 0!
      let dd = d.getDate();

      if (dd < 10) dd = "0" + dd;
      if (mm < 10) mm = "0" + mm;

      const returnDate = dd + "/" + mm + "/" + yyyy;
      return returnDate;
    },
  },
};
